import React, { Fragment } from "react";
import cx from "classnames";

import { connect } from "react-redux";

import CSSTransitionGroup from "react-transition-group/CSSTransitionGroup";

import HeaderLogo from "../AppLogo";

import SearchBox from "./Components/SearchBox";
import MegaMenu from "./Components/MegaMenu";
import UserBox from "./Components/UserBox";
import HeaderRightDrawer from "./Components/HeaderRightDrawer";

import HeaderDots from "./Components/HeaderDots";
import {browserHistory} from 'react-router';
import {
  setUserData,
} from '../../reducers/ThemeOptions';
class Header extends React.Component {
  async componentWillMount() {
    this.HandleCheck();
    var empty=[];
    if(localStorage.getItem('user') != 'undefined' ) 
    {
      empty=JSON.parse(localStorage.getItem('user'));
    }
    this.HandleUserData(empty);
  }
  HandleCheck = async(e)=>{
    let flag=await localStorage.getItem("isLogin");
    if(!flag)
    {
      browserHistory.push("/#/user/login");
      window.location.reload();
    }
  }
  HandleUserData = (data) => {
    let {setUserData } = this.props;
    setUserData(data);
  };
  render() {
    let {
      headerBackgroundColor,
      enableMobileMenuSmall,
      enableHeaderShadow,
    } = this.props;
    return (
      <Fragment>
        <CSSTransitionGroup component="div"
          className={cx("app-header", headerBackgroundColor, {
            "header-shadow": enableHeaderShadow,
          })}
          transitionName="HeaderAnimation" transitionAppear={true} transitionAppearTimeout={1500}
          transitionEnter={false} transitionLeave={false}>
          <HeaderLogo />
          <div className={cx("app-header__content", {
              "header-mobile-open": enableMobileMenuSmall,
            })}>
            <div className="app-header-right">
              {/*<HeaderDots />*/}
              <UserBox />
            </div>
          </div>
        </CSSTransitionGroup>
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => ({
  enableHeaderShadow: state.ThemeOptions.enableHeaderShadow,
  closedSmallerSidebar: state.ThemeOptions.closedSmallerSidebar,
  headerBackgroundColor: state.ThemeOptions.headerBackgroundColor,
  enableMobileMenuSmall: state.ThemeOptions.enableMobileMenuSmall,
  userData: state.ThemeOptions.userData,
});

const mapDispatchToProps = (dispatch) => ({
  setUserData:enable=>dispatch(setUserData(enable)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Header);
