import React, { Fragment } from "react";

import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
  Card,
  CardBody,
  CardHeader,
  CardFooter,
  Button,
  ButtonGroup,
  UncontrolledButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Container,
  CardTitle,
  Collapse,
  ListGroup,
  ListGroupItem
} from "reactstrap";
// import {
//   setClientWizard,
//   setClientName,
// } from '../../../../../reducers/ThemeOptions';
import ApiCall from "../../../../Services/Function/ApiCall";
import {connect} from 'react-redux';

class ClientDetailsListing extends React.Component {
  getDetails=async(email)=>
  {
    let data={'steps':2,'email':email,'id':this.props.clientWizard['id'],'data':this.props.clientWizard['data']};
    let { clientWizard, setClientWizard } = this.props;
    await setClientWizard(data);
  }
  async componentWillMount() {
    // let {setClientName } = this.props;
    // await setClientName(this.props.clientWizard['data']['title']);
  }
  HandleDivContent=()=>
  {
     let data=
      <ListGroup flush>
        <ListGroupItem className="card-hover-shadow">
          <div className="widget-content p-0">
            <div className="widget-content-wrapper">
              <div className="widget-content-left">
                <div className="widget-heading">{this.props.clientWizard['data']['ceo_name']} | {this.props.clientWizard['data']['ceo_designation']} | {this.props.clientWizard['data']['ceo']} </div>
              </div>
            </div>
            <div className="widget-content-wrapper">
              <div className="widget-content-right">
                <Button className="mb-2 mr-2 btn btn-shadow btn-primary" color="primary"
                  onClick={() =>this.getDetails(this.props.clientWizard['data']['ceo'])} >
                  Invite
                </Button>
              </div>
            </div>
          </div>
        </ListGroupItem>
        <ListGroupItem className="card-hover-shadow">
          <div className="widget-content p-0">
            <div className="widget-content-wrapper">
              <div className="widget-content-left">
                <div className="widget-heading">{this.props.clientWizard['data']['liaison_one_name']} | {this.props.clientWizard['data']['liaison_one_designation']} | {this.props.clientWizard['data']['liaison_one']}</div>
              </div>
            </div>
            <div className="widget-content-wrapper">
              <div className="widget-content-right">
                <Button className="mb-2 mr-2 btn btn-shadow btn-primary" color="primary"
                  onClick={() =>this.getDetails(this.props.clientWizard['data']['liaison_one'])} >
                  Invite
                </Button>
              </div>
            </div>
          </div>
        </ListGroupItem>
        <ListGroupItem className="card-hover-shadow">
          <div className="widget-content p-0">
            <div className="widget-content-wrapper">
              <div className="widget-content-left">
                <div className="widget-heading">
                  {this.props.clientWizard['data']['liaison_two_name']} | {this.props.clientWizard['data']['liaison_two_designation']} | {this.props.clientWizard['data']['liaison_two']}</div>
              </div>
            </div>
             <div className="widget-content-wrapper">
              <div className="widget-content-right">
                <Button className="mb-2 mr-2 btn btn-shadow btn-primary" color="primary"
                  onClick={() =>this.getDetails(this.props.clientWizard['data']['liaison_two'])} >
                  Invite
                </Button>
              </div>
            </div>
          </div>
        </ListGroupItem>
      </ListGroup>
    return data;
  }
  render() {
    return (
      <Fragment>
       {this.HandleDivContent()}
      </Fragment>
    );
  }
}
export default connect()(ClientDetailsListing);

