import React, { Fragment } from "react";
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
  Card,
  CardBody,
  CardHeader,
  CardFooter,
  Button,
  ButtonGroup,
  UncontrolledButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Container,
  CardTitle,
  Collapse,
  ListGroup,
  ListGroupItem
} from "reactstrap";
import {
  setClientWizard,
  setClientName,
} from '../../../../../reducers/ThemeOptions';
import Loader from "react-loaders";
import ApiCall from "../../../../Services/Function/ApiCall";
import {connect} from 'react-redux';
import SweetAlert from "sweetalert-react";
class EmailCheck extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      flag: false,
      message:null,
    };
  }
  getDetails=async()=>
  {
    let { clientWizard, setClientWizard } = this.props;
    let data={'steps':3,'id':2};
    await setClientWizard(data);
    console.log(this.props.clientWizard);
  }
  async componentWillMount() {
    let data={'email':this.props.clientWizard['email'],'id':this.props.clientWizard['data']['id']};
    let resgister_email=await ApiCall.postAPICall(2,'register/email',data);
    if(resgister_email.status)
    {
      let response=await ApiCall.postAPICall(1,'get/check/email',data);
      if(response.success)
      {
        this.getDetails();
        this.setState({
          flag:false
        });
      }
      else
      {
        this.setState({
          flag:true
        });
        this.setState({
          message:response.message
        });
      }
    }
    else
    {
      this.setState({
        flag:true
      });
      this.setState({
        message:'Email Already Exist'
      });
    }

    }
  toggleEnableShow=async()=>
  {
    await this.setState({
      flag:false
    });
    let data={'steps':1,'id':this.props.clientWizard['id'],'data':this.props.clientWizard['data']};
    let { clientWizard, setClientWizard } = this.props;
    await setClientWizard(data);
  }
  HandleDivContent=()=>
  {
    let data= 
      <Col md="12">
       <Card className="card-hover-shadow card-border mb-3">
         <CardHeader >
           <i className="header-icon lnr-envelope icon-gradient bg-happy-itmeo">
             {" "}
           </i>
           Email Checking Process
         </CardHeader>
         <CardBody>
            <Col md="12">
              <div className="mr-3 mb-3">
                <div className=" d-flex justify-content-center align-items-center">
                  <Loader type="pacman" />
                </div>
              </div>
            </Col>
         </CardBody>
         <CardFooter className="d-block text-center">
            <span className="widget-heading">We are Checking Your Email..!</span>
         </CardFooter>
       </Card>
      </Col>
    return data;
  }
  render() {
    console.log(this.props);
    return (
      <Fragment>
      <SweetAlert confirmButtonColor="" confirmButtonText="Close" title={this.state.message} show={this.state.flag} type="error" onConfirm={() => this.toggleEnableShow()}/>
      <Row>
       {this.HandleDivContent()}
      </Row>
      </Fragment>
    );
  }
}
const mapStateToProps = state => ({
  clientWizard: state.ThemeOptions.clientWizard,
  clientName:state.ThemeOptions.clientName,
});

const mapDispatchToProps = dispatch => ({
  setClientWizard:enable=>dispatch(setClientWizard(enable)),
  setClientName:enable=>dispatch(setClientName(enable)),
});

export default connect(mapStateToProps, mapDispatchToProps)(EmailCheck);

