import React, { Fragment } from "react";
import {
  Input,
  InputGroup,
  InputGroupAddon,
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
  Card,
  CardBody,
  CardHeader,
  CardFooter,
  Button,
  ButtonGroup,
  UncontrolledButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Container,
  CardTitle,
  Collapse,
  ListGroup,
  ListGroupItem
} from "reactstrap";
import {
  setClientWizard,
  setClientId,
  setClientArray,
  setAccountName,
  setClientName,
} from '../../../../../reducers/ThemeOptions';
import {
  faSearch,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import ApiCall from "../../../../Services/Function/ApiCall";
import SweetAlert from "sweetalert-react";
import {connect} from 'react-redux';
import LaddaButton, {
  EXPAND_LEFT,
} from "react-ladda";
class ClientListing extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      expLeft:{},
      clients: {},
      error_flag:false,
      search:null,
      account:null,
    };
  }
  toggle(id,flag) {
    let temp =this.state.expLeft;
    temp[id]=flag;
    this.setState({
      expLeft: temp,
      progress: 0.5,
    });
  }
  getDetails=async(id)=>
  {
    await this.toggle(id,true);
    let temp={'id':id};
    let response=await ApiCall.postAPICall(1,'get/client/details',temp);
    if(response.data)
    {
      let { clientWizard, setClientWizard } = this.props;
      let data={'steps':1,'id':id,'data':response['data']};
      await setClientWizard(data);
    }
    else
    {
      this.setState({
        error_flag:true
      });
    }
    await this.toggle(id,false);
  }
  async componentWillMount() {
    let {setClientName } = this.props;
    await setClientName('');
  }
  async componentDidUpdate(prevProps) {
    if (this.props.clientId !== prevProps.clientId) {
      let str=this.props.clientId;
      let data={'token':str};
      let response=await ApiCall.postAPICall(1,'get/opinion',data);
      let {setAccountName } = this.props;
      await setAccountName(response.user.display_name);
      let {setClientArray } = this.props;
      await setClientArray(response.data);
    }
  }
  toggleEnableShow=()=>
  {
    this.setState({
      error_flag:false
    });
  }
  HandleDivContent=(name,id)=>
  {
    if(name)
    {
      let flag=(this.state.search && this.state.search.length > 0) ? name.toLowerCase().includes(this.state.search.toLowerCase()) : true;
      var data=null;
      if(flag)
      {
        data=
        <ListGroupItem className="card-hover-shadow">
          <div className="widget-content p-0">
            <div className="widget-content-wrapper">
              <div className="widget-content-left">
                <div className="widget-heading">{name}</div>
              </div>
            </div>
            <div className="widget-content-wrapper">
              <div className="widget-content-right">
                <LaddaButton className="mb-2 mr-2 btn btn-shadow btn-primary" loading={this.state.expLeft[id]}
                  onClick={() =>this.getDetails(id)} data-style={EXPAND_LEFT}>
                  Details
                </LaddaButton>
              </div>
            </div>
          </div>
        </ListGroupItem>;
      }
      return data;
    }
  }
  handleSearch=(e)=>
  {
    this.setState({
      search: e.target.value
    });
  }
  render() {
    return (
      <Fragment>
        <SweetAlert confirmButtonColor="" confirmButtonText="Close" title={'Data Not Found'} show={this.state.error_flag} type="error" onConfirm={() => this.toggleEnableShow()}/>
        <Col lg="6" md="6" sm="6" className="mb-4" style={{margin:'auto'}}>
          <InputGroup>
            <Input placeholder="" onChange={(e) =>this.handleSearch(e)} />
            {<InputGroupAddon addonType="append">
              <Button className=" mr-2 btn-icon" color="primary">
                <FontAwesomeIcon className="btn-icon-wrapper" icon={faSearch} size="4x" />
              </Button>  
            </InputGroupAddon>}
             
          </InputGroup>
        </Col>
        {Object.keys(this.props.clientArray).map((val, i) => (
          this.HandleDivContent(this.props.clientArray[i]['name'],this.props.clientArray[i]['id'])
        ))
        }
      </Fragment>
    );
  }
}
const mapStateToProps = state => ({
  clientWizard: state.ThemeOptions.clientWizard,
  clientId: state.ThemeOptions.clientId,
  clientArray:state.ThemeOptions.clientArray,
  accountName:state.ThemeOptions.accountName,
  clientName:state.ThemeOptions.clientName,
});

const mapDispatchToProps = dispatch => ({
  setClientWizard:enable=>dispatch(setClientWizard(enable)),
  setClientId:enable=>dispatch(setClientId(enable)),
  setClientArray:enable=>dispatch(setClientArray(enable)),
  setAccountName:enable=>dispatch(setAccountName(enable)),
  setClientName:enable=>dispatch(setClientName(enable)),
});

export default connect(mapStateToProps, mapDispatchToProps)(ClientListing);
